'use client';

import { useRouter } from 'next/navigation';
import { AppBar, IconButton, Typography, Toolbar } from '@mui/material';
import { ArrowBackRounded } from '@mui/icons-material';
import { colors } from '@/theme';

interface CharityDetailsHeaderProps {
  pageTitle: string;
}

const SimpleHeader: React.FC<CharityDetailsHeaderProps> = ({ pageTitle }) => {
  const router = useRouter();

  return (
    <AppBar
      position="sticky"
      elevation={0}
      sx={{
        backgroundColor: colors.secondary,
        borderBottom: `1px solid ${colors.shadow}`,
      }}
    >
      <Toolbar style={{ justifyContent: 'space-between' }}>
        <IconButton edge="start" onClick={() => router.back()} sx={{ color: colors.secondaryPale }}>
          <ArrowBackRounded />
        </IconButton>
        <Typography
          variant="h6"
          sx={{
            color: colors.secondaryPale,
            fontWeight: 600,
            fontSize: '16px',
            flex: 1,
            textAlign: 'center',
            px: 2,
          }}
        >
          {pageTitle}
        </Typography>
      </Toolbar>
    </AppBar>
  );
};

export default SimpleHeader;
